import React from "react";
import { ethers } from "ethers";
import './App.css'

class Contract extends React.Component {
    constructor() {
        super();
        this.state = {
            address: "",
            signer: null,
            ballotFactoryContract: null,
            adminAddr:"",
            ballotAddr: ""
        };
    }

    connectWallet = async () => {
        let provider;
        let signer;
        if (window.ethereum == null) {
            console.log("MetaMask not installed; using read-only defaults")
            provider = ethers.getDefaultProvider()
        
        } else {        
            provider = new ethers.providers.Web3Provider(window.ethereum);
            signer = await provider.getSigner();
            this.setState({signer: signer})

            const accounts = await provider.send("eth_requestAccounts", []);
            this.setState({address: accounts[0]});
        }

        try {
            //合约地址
            const ballotFactoryContractAddr = "0xD9e16F851542BE85210eDEC9483343F62AAc1B05";
            //合约ABI
            const response = require("./abi/BallotFactory.json");
            const abi = response.abi;
            //创建合约对象
            // const contract = new ethers.Contract(ballotFactoryContractAddr, abi, provider); //不需要签名
            const contract = new ethers.Contract(ballotFactoryContractAddr, abi, signer); //需要签名
            this.setState({ballotFactoryContract: contract});

        } catch (error) {
            console.error("Error reading JSON file: ", error);
        }
    }

    getOwner = async () => {
        let owner = await this.state.ballotFactoryContract.owner();
        this.setState({adminAddr: owner});
    }

    createVote = async () => {
        // 组织工厂合约地址
        const orgFactoryAddr = "0x04b0DA3ae06162de939A49183BdfEb9E7922B2F4";
        // Token合约地址
        const tokenAddr = "0x1f4D742577F5e7ecF7a30040A6a9863E6eB6067a";
        // 根组织合约地址
        const rootOrgAddr = "0x478d5652A6c917749ccd4EA38bAC7fC1ed64430e";
        // 治理激励合约地址
        const excitationAddr = "0x6C36075fD9d086A855459AB6d0d2d52DDBCC5fEF";


        // 调用合约方法（消息）
        const owner_factory = await this.state.ballotFactoryContract.owner();  
        console.log("投票工厂合约管理员地址: ", owner_factory);


        //调用合约方法（交易）- 创建投票合约
        console.log("-----------创建投票-----------")
         //当前时间
        const now = new Date();
        //过期时间
        const deadline = parseInt((now.setMinutes(now.getMinutes() + parseInt(3))) / 1000);

        const topic = ethers.utils.keccak256(ethers.utils.toUtf8Bytes("创建新组织的提案"));

        let ballotParamInfo = {
            id: "001",
            stakedTokens: 10,
            topic: topic,
            deadline: deadline,
            passPercent: 100,
            tokenAddr: tokenAddr,
            excitationAddr: excitationAddr,
            voters: [this.state.address],
            calldataContracts: [],
            calldataValues: [],
        }
        console.log("-------生成创建组织的指令集 -------");

        //指令集 - 函数参数
        const orgAId = "001";  //组织ID
        const rules = "规章制度";  //章程

        //指令集 - Solidity中函数的名称和参数类型
        const functionName = "createOrg";
        const functionParamType = ["string", "string",  "address", "address"];

        //指令集 - 参数类型及参数值 
        const paramType = ["string", "string", "address", "address"];;
        const paramVal = [orgAId, rules, rootOrgAddr, this.state.address];

        //生成指令集
        // 用于编码函数名称和参数类型的selector
        const selector = ethers.utils.hexDataSlice(ethers.utils.keccak256(ethers.utils.toUtf8Bytes(`${functionName}(${functionParamType.join(',')})`)), 0, 4);

        //用于编码函数参数的ABI编码器
        const encodedData = ethers.utils.defaultAbiCoder.encode(
            paramType,
            paramVal
        );

        //构造包含函数选择器的payload
        const payload = selector + encodedData.substr(2); // 去除前面的'0x'

        //修改指令集
        ballotParamInfo.calldataContracts = [orgFactoryAddr]
        ballotParamInfo.calldataValues = [payload]

        //创建投票合约（部署投票合约）
        console.log("-------创建投票合约-------"); 

        const options = {
            gasLimit: 7000000, // 可以设置为您需要的任何数量
        }

        // 调用投票工厂合约中创建投票方法
        const txResponse = await this.state.ballotFactoryContract.connect(this.state.signer).createBallot(rootOrgAddr, this.state.address, ballotParamInfo, options);
        console.log("txResponse: ", txResponse);
        const txReceipt = await txResponse.wait();
        console.log("txReceipt: ", txReceipt);

        //查看事件中的投票合约地址
        const ballotAddr = txReceipt.events[0].args._ballotAddr;
        this.setState({ballotAddr: ballotAddr});
        console.log("投票合约地址：", ballotAddr);

    }

    
    render() {
        return (
            <div>
                 <div className="text-center wow fadeInUp">
                    <h1>Ethers V5 调用合约样式 </h1>
                </div>

                <div>
                    <button onClick={this.connectWallet}>连接钱包</button>
                    <div className="css_addr">
                        {this.state.address == "" ? "" : this.state.address}
                    </div>
                </div>
                <br/>
                <div>
                    <button onClick={this.getOwner}>获取投票工厂合约管理员地址</button>
                    <div className="css_addr">
                        {this.state.adminAddr == "" ? "" : "投票工厂合约管理员地址：" + this.state.adminAddr}
                    </div>
                </div>
                <br/>
                <div>
                    <button onClick={this.createVote}>创建投票（创建组织）</button>
                    <div className="css_addr">
                        {this.state.ballotAddr == "" ? "" : "投票合约地址：" + this.state.ballotAddr}
                    </div>
                </div>
                <br/>
                <div className="css_addr">
                    <div className="information">
                        <ul>
                        使用说明：<br/>
                        以创建组织的投票为例<br/>
                        <li>首先需要 “连接钱包”</li>
                        <li>“获取投票工厂合约管理员地址” - 调用合约方法（消息），不需要签名，也不会消耗gas</li>
                        <li>“创建投票（创建组织）” - 调用合约方法（交易），需要签名，会消耗gas</li>
                        </ul>
                    </div>
                </div>
                <br/>

            </div>
        )

    }
}


export default Contract